background: rgba(36, 81, 122, 1);
border-radius: 100%;
}

swiper-container {
  width: 100%;
  margin: auto;
  box-sizing: border-box;
  overflow: visible;
  margin-top: 5px;
}

swiper-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 50px;
  width: 640px;
  height: 244px;
  border-radius: 20px;
  box-shadow: 0px 0px 8px rgba(36, 81, 122, 0.1);
  background: linear-gradient(180deg, #24517A 0%, rgba(66, 149, 224, 0.7) 100%);
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  text-align: center;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0.4;
  transform: scale(0.9);
}

swiper-slide.swiper-slide-active {
  opacity: 1;
  transform: scale(1);
}

.slide-text-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 70px;
}

.slide-text1{
font-family: 'Public Sans';
